import axios from 'axios';
import cryptoJS from 'crypto-js'
import helper from './helper.js'
import {message} from 'ant-design-vue';
const request = {}

let configUrl = ''
const webtype = process.env.NODE_ENV === 'development' ? false:true//true,fasle
configUrl = webtype ? 'http://api.nnshfw.com' : 'http://192.168.31.218:8100'
const instance = axios.create({
	timeout: 5000
})

//
// ,
instance.interceptors.request.use(
	function(config) {
		// console.group('')
		// console.log(config)
		// console.groupEnd()
		// config.headers.token = '123456'
		return config
	},
	function(err) {
		message.error('')
		return Promise.reject(err)
	}
)

//
//
//
//404 404 500
instance.interceptors.response.use(
	function(config) {
		// console.group('')
		// console.groupEnd()
		return config
	},
	function(err) {
		message.error(',')
		console.log(err)
		return Promise.reject(err)
	}
)
let header = {
    encryptionKey: 'telo-origin', // /key
    encryptionKey2: 'telo-auth', //  /key
    encrypt: 'telo-data-encrypt', // 
    // timestamp: 'telo-timestamp', // 
	timestamp:'telo-time-param'
}


request.globalRequest = async (url, method, data, headers) => {
	// console.log(url, method, data, headers)
	let result = ''
	if(method === 'GET'){
		result = await instance.get(configUrl+url, {params: {...data},headers:{...headers}})
	}else if(method === 'POST'){
		result = await instance.post(configUrl+url,data,{headers:{...headers}})
	}else if(method === 'DELETE'){
		result = await instance.delete(configUrl+url, {params: {...data},headers:{...headers}})
	}else if(method === 'PUT'){
		 result = await instance.put(configUrl+url,data,{headers:{...headers}})
	}
	let encrypt = result.headers[header.encrypt]
	let timestamp = result.headers[header.timestamp]
	let datas = result
	if(encrypt === 'true' && timestamp != ""){
			let key = header.timestamp + timestamp //key+
		    result =  helper.aesDecrypt(datas.data.data, key) //key
		    if (typeof result === "string") { //json
		        result = JSON.parse(result)
		    }else{
				result = result.data
			}
	}else{
		result = result.data
	}
	return result
}
export default request
